@font-face {
  font-family: "Roboto Condensed", sans-serif;
  src: url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed", sans-serif;
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PT Mono", monospace;
  src: url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto", sans-serif;
  src: url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

body {
  font-weight: 400;
  font-size: 14px;
  color: #111111;
  min-width: 320px;
  padding: 0;
  margin: 0;
  font-family: "PT Mono", monospace, "Arial", sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
::after,
::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

a {
  text-decoration: none; }

p,
ul {
  padding: 0;
  margin: 0;
  list-style-type: none; }

.page-header {
  width: 100%;
  background-color: #1B2931; }

.page-header__wrap {
  max-width: 1452px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 34px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.page-header__logo {
  display: inherit; }

.page-header__logo-text {
  margin: 4px 0px 0 16px; }

.page-header__logo-text p {
  font-family: "Roboto Condensed", sans-serif;
  color: #fff;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px; }

.page-header__logo-text span {
  font-family: "Roboto", sans-serif;
  color: #788891;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px; }

.page-header__form ul {
  margin: 2px 3px 0 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.page-header__form ul a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  color: #fff; }

.page-header__form li {
  margin-left: 48px; }

.page-header__form li:nth-child(1) {
  margin-right: 42px; }

.page-header__form li:nth-child(1) {
  margin-left: 0; }

.page-header__form ul img {
  margin-right: 16px; }

.page-header__form ul li p:nth-child(1) {
  font-family: "Roboto Condensed", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  border: 1px solid #fff;
  width: 180px;
  min-height: 45px;
  padding: 14px 23px 13px 25px;
  border-radius: 8px;
  letter-spacing: 1px; }

@media (max-width: 1024px) {
  .page-header__form li {
    margin-left: 10px; }
  .page-header__form li:nth-child(1) {
    margin-right: 0px; }
  .page-header__form ul img {
    margin-right: 10px; } }

@media (max-width: 768px) {
  .page-header__wrap {
    padding-left: 25px;
    padding-top: 22px; }
  .page-header__form ul p {
    display: none; }
  .page-header__form ul img {
    width: 25px;
    height: 18px;
    margin-right: 0; }
  .page-header__form li:nth-child(2) img {
    width: 25px;
    height: 24px; }
  .page-header__form li {
    margin-left: 3px; }
  .page-header__form ul {
    margin: 10px 0 0 0; }
  .page-header__form li:nth-child(1) {
    margin-right: 30px; } }

@media (max-width: 320px) {
  .page-header__wrap {
    padding-left: 17px;
    padding-top: 17px; }
  .page-header__logo-img img {
    width: 38px;
    height: 38px; }
  .page-header__logo-text {
    display: none; }
  .page-header__form ul {
    margin: 6px 0 0 0; }
  .page-header__form li {
    margin-left: -5px; } }
